#Resizeable-Console-Page {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-height: 100%;
    width: 100%;
    overflow: hidden;
}

.moveHandle {
    width: 24px;
    height: 24px;
    font-size: 12px;
    line-height: 24px;
    position: fixed;
    background-color: rgb(111, 175, 47);
}

.Resizeable-Plotly {
    position: relative;
    -webkit-flex: 2 1;
            flex: 2 1;
}

.Resizeable-Cam {
    position: relative;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.Resizeable-Row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 50vh;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}


.RND-Plotly {
    position: relative !important;
}

.RND-Cam {
    position: relative !important;
}

.Rnd-View {
    overflow: auto;
    width: 100%;
    height: 100%;
}

.Rnd-View-Vegger {
    overflow: auto;
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-width: 100%;
}

.Rnd-View-Vegger img {
